.switch {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 200px;
  width: 40px;
  height: 20px;
  margin-left: 700px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

@media (width <= 768px) {
  .switch {
    margin-left: 101px;
  }
}

@media (width <= 375px) {
  .switch {
    margin-left: 144px;
  }
}

.switch:after {
  content: "";
  background: #f7f7f7;
  border-radius: 180px;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 0 5px 10px #0003;
}

.switch p {
  display: none;
}

.dark {
  visibility: hidden;
  width: 0;
  height: 0;
}

.dark:checked + .switch {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

.dark:checked + .switch:after {
  background: none, #f7f7f7;
  right: 24px;
  transform: translateX(110%);
}

.switch, .switch:after {
  transition: all .3s;
}

